import useTypedSelector from '../store/selectors';

function useStages() {
  const {
    city, pickPoint, model, dateFrom, dateTo, tariff,
  } = useTypedSelector((state) => state.form);

  //     ,      
  return [
    //      
    //  -    required
    //  -   
    {
      name: '',
      vars: [city, pickPoint],
      buttonLabel: ' ',
    },
    {
      name: '',
      vars: [model],
      buttonLabel: '',
    },
    {
      name: '',
      vars: [dateFrom, dateTo, tariff],
      buttonLabel: '',
    },
    {
      name: '',
      vars: [],
      buttonLabel: '',
    },
  ];
}

export default useStages;
